:root {
  --color-main: #333;
}

* {
  margin: 0;
  padding: 0;
}

*,
*:before,
*:after {
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
}

body {
  color: var(--color-main);
}

.title {
  font-size: 1.5rem;
  margin: 2rem;
}

.current-player {
  margin: 2rem;
  text-align: center;
}

.game-board {
  text-align: center;
  width: 100%;
  border: thin solid #ddd;
}

.score-board {
  position: fixed;
  text-align: left;
  top: 70%;
  left: 50%;
  margin-left: -10.2rem;
  font-size: 2rem;
}

.previous-winners {
  margin: 2rem;
  font-size: 1.5rem;
}

/* .body {
  display: flex;
} */

.game {
  position: fixed;
  top: 5%;
  left: 50%;
  margin-left: -13.7rem;
  /* margin-left: -25%; */
  text-align: center;
}

.slot {
  background-color: #eee;
  border: thin solid #ddd;
  width: 5rem;
  height: 5rem;
  margin: 1rem;
  font-size: 3rem;
  vertical-align: top;
}

.slot:hover {
  background-color: #369ee3;
  color: white;
}

.slot:focus,
.number:active {
  outline: none;
  border: thin solid white;
}

.border {
  position: relative;
  text-align: center;
}
.inner {
  display: inline-block;
}

#play-again {
  text-align: center;
  background: none;
  padding: 1rem;
  color: white;
  font-size: 1.5rem;
  background-color: #369ee3;
  border: thin solid white;
}

button {
  border-radius: 0.4rem;
}
